<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/program')" class="current">프로그램</li>
			<li @click="goto('/admin/goods')">굿즈</li>
		</ul>

		<div class="contents-container">
			<p class="contents-title underline marB-20 body3">새 프로그램 등록</p>
			<form method="post" enctype="multipart/form-data">
				<div class="input-container marB-40">
					<section class="marB-40">
						<p class="body3 marB-10">대표 이미지</p>
						<div class="input-item align-top">
							<input type="file" id="files1" name="file" />
							<AddImageSingle
								v-if="thumb"
								:refName="'mainImage'"
								@change="setMainImage"
								:image="thumb"
							/>
						</div>
					</section>

					<section class="basic-info">
						<p class="body3 marB-20">기본 정보</p>

						<div class="input-item">
							<p class="small-title">프로그램명</p>
							<input
								type="text"
								class="admin-input medium"
								name="contents"
								v-model="programName"
							/>
						</div>
						<div class="input-item">
							<p class="small-title">진행자</p>
							<input
								type="text"
								class="admin-input medium"
								name="contents"
								v-model="coach"
							/>
						</div>
						<div class="input-item">
							<p class="small-title">일자</p>
							<input
								type="text"
								class="admin-input medium"
								name="contents"
								v-model="classDate"
							/>
						</div>
						<div class="input-item">
							<p class="small-title">시간</p>
							<input
								type="text"
								class="admin-input medium"
								name="contents"
								v-model="classTime"
							/>
						</div>
						<div class="input-item">
							<p class="small-title">가격</p>
							<input
								type="text"
								class="admin-input medium"
								name="contents"
								v-model="programPrice"
							/>
						</div>
					</section>

					<section class="options">
						<p class="body3 marB-20">옵션</p>

						<div
							v-for="(option, index) in options"
							:key="option.id"
							class="input-item marB-20"
						>
							<p class="small-title">옵션 {{ index + 1 }}</p>
							<div class="input-item-body">
								<input
									type="hidden"
									class="admin-input medium"
									v-model="option.id"
								/>
								<input
									type="text"
									class="admin-input medium"
									v-model="option.classDateTime"
									placeholder="날짜"
								/>
								<input
									type="text"
									class="admin-input small"
									v-model="option.userCount"
									placeholder="참여인원수"
								/>
								<input
									type="text"
									class="admin-input small"
									v-model="option.addPrice"
									placeholder="추가가격"
								/>
								<div @click="removeField(index)">
									<CloseRed />
								</div>
							</div>
						</div>

						<button
							class="solid-btn green marB-30"
							type="button"
							@click="addField"
						>
							옵션 추가
						</button>
					</section>
					<LoadingSpinner :loading="isLoading" />
					<section class="marB-40">
						<div class="img-upload-container">
							<p class="body3">상세 이미지</p>
							<div class="input-item align-top">
								<input type="file" id="files2" name="file" />
								<AddImageSingle
									v-if="detailImg"
									:refName="'detailImage'"
									@change="setDetailImage"
									:image="detailImg"
								/>
							</div>
						</div>
					</section>
					<button class="outlined-btn" type="button" @click="updateProgram">
						등록
					</button>
				</div>
			</form>
		</div>
	</div>
</template>

<script>
import Store from '@/store';
import { mapGetters } from 'vuex';
import AddImageSingle from '@/components/common/AddImageSingle';
import { fetchProgramDetail, updateProgram } from '@/api/program';
import CloseRed from '@/assets/icon/program_goods/CloseRed';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	components: { AddImageSingle, CloseRed, LoadingSpinner },
	data() {
		return {
			id: this.$route.params.id,
			thumb: '',
			detailImg: '',
			programName: '',
			coach: '',
			classDate: '',
			classTime: '',
			programPrice: '',
			options: [
				{
					classDateTime: '',
					userCount: '',
					addPrice: '',
				},
			],
			isLoading: false,
		};
	},
	created() {
		Store.commit('SET_MAIN', null);
		Store.commit('SET_DETAIL', null);
		this.id = this.$route.params.id;
		this.fetchProgramDetail(this.id);
	},
	computed: {
		...mapGetters(['main', 'detail']),
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		addField() {
			this.options.push({
				classDateTime: '',
				userCount: '',
				addPrice: '',
			});
		},
		async fetchProgramDetail(id) {
			let result;
			this.isLoading = true;
			try {
				result = await fetchProgramDetail(id);
				this.programName = result.data.title;
				this.coach = result.data.coach;
				this.classDate = result.data.classDate;
				this.classTime = result.data.classTime;
				this.programPrice = result.data.price;
				this.options = result.data.options;
				this.thumb = {
					imgPath: result.data.thumbnailImgPath,
					imgName: result.data.thumbnailImgName,
				};
				this.detailImg = {
					imgPath: result.data.detailImgPath,
					imgName: result.data.detailImgName,
				};
			} catch (e) {
				console.log(e);
			} finally {
				this.isLoading = false;
			}
		},
		removeField(index) {
			this.options.splice(index, 1);
		},
		async updateProgram() {
			let data = new FormData();
			if (this.main) {
				data.append('thumbnailFile', this.main);
			}
			if (this.detail) {
				data.append('detailFile', this.detail);
			}
			data.append('title', this.programName);
			data.append('coach', this.coach);
			data.append('classDate', this.classDate);
			data.append('classTime', this.classTime);
			data.append('price', this.programPrice);
			data.append('optionList', JSON.stringify(this.options));
			this.isLoading = true;
			try {
				let result = await updateProgram(this.id, data);
				console.log(result);
				if (result.status == 200) {
					alert('프로그램이 수정되었습니다');
					// this.$router.go();
				} else {
					alert('에러가 발생했습니다');
				}
			} catch (e) {
				alert('에러가 발생했습니다');
			} finally {
				this.isLoading = false;
			}
		},
		setMainImage(data) {
			Store.commit('SET_MAIN', data);
			this.thumb = this.main;
		},
		setDetailImage(data) {
			Store.commit('SET_DETAIL', data);
			this.detailImg = this.detail;
		},
	},
};
</script>

<style scoped>
.basic-info {
	margin-bottom: 40px;
}
.input-item-body {
	display: flex;
	align-items: center;
}

.options {
	margin-bottom: 40px;
}
</style>
